				},
				method:'POST',
				data: {
					uid:that.user.uid
				},
		    success: (res) => {
				if(res.data.flag!=500){
					that.flagHuiYuan=((res.data.aa.rolestart+res.data.aa.roletime)>=res.data.flag?true:false)
